
import { defineComponent, ref, toRaw, watch } from 'vue';

export default defineComponent({
  name: 'CCSection',
  props: {
    name: {
      type: String,
    },
    expand: {
      type: Boolean,
      default: true,
    },
  },
  emit: ['change'],
  setup(props, { emit }) {
    const expand = ref(props.expand || false);
    watch(
      () => props.expand,
      (v) => {
        expand.value = !!v;
      }
    );
    const name = ref(props.name || '');
    watch(
      () => props.name,
      (v) => {
        name.value = v || '';
      }
    );
    return {
      expand,
      name,
      onExpand() {
        expand.value = !expand.value;
        emit('change', toRaw(expand.value));
      },
    };
  },
});
